import GlobalSettings from '@/GlobalSettings'
import { IKDataEntity } from 'metaflow-js'
import hillo from 'hillo'
import AttributeGroup from './AttributeGroup'
import CategoryType from './CategoryType'
import store from '@/store'

const entity = {
  id: {
    type: IKDataEntity.Types.Integer,
    displayName: 'id',
    tableConfig: {
      align: 'start',
    },
    form: false,
  },
  name: {
    displayName: 'name',
  },
  image: {
    type: IKDataEntity.Types.Image,
    formConfig: {
      required: false,
      type: {
        root: () => store.getters.rootUrl + GlobalSettings.dcImageRoot,
      },
    },
  },
  attributeGroupId: {
    displayName: 'groupName',
    type: IKDataEntity.Types.Option,
    formConfig: {
      type: {
        selectItems: AttributeGroup.forceGetList,
      },
    },
  },
  isMultiChosen: {
    type: IKDataEntity.Types.Boolean,
  },
  value: {
    displayName: 'value',
    formConfig: {
      default: 1,
    },
  },
  printMod: {
    displayName: 'printMod',
  },
  priceMod: {
    type: IKDataEntity.Types.Float,
    displayName: 'priceMod',
    formConfig: {
      overwriteRule: true,
      rule: [
        v => /^(-)?[0-9]+\.{0,1}[0-9]{0,2}$/.test(v) || 'please enter the correct price',
      ],
    },
  },
  dishesCategoryTypeId: {
    displayName: 'warengruppe',
    type: IKDataEntity.Types.Option,
    formConfig: {
      type: {
        selectItems: CategoryType.getList,
      },
    },
  },
  isAsDefault: {
    type: IKDataEntity.Types.Boolean,
  },
  isActive: {
    type: IKDataEntity.Types.Boolean,
  },
  frontendHide: {
    type: IKDataEntity.Types.Boolean,
  },

}
const config = {
  add: function (item) {
    return hillo.postWithUploadFile('Category.php?op=addAttribute', item, {
      showLoading: true,
    })
  },
  edit: function (item) {
    return hillo.postWithUploadFile('Category.php?op=updateAttribute', item, {
      showLoading: true,
    })
  },
  remove: function (id) {
    return hillo.post('Category.php?op=deleteAttribute', { id: id })
  },
  load: async function (filter) {
    return (await hillo.get('Category.php', {
      op: 'showAttribute',
      lang: GlobalSettings.getLang(),
      ...filter,
    }))
      .content
  },
}

export default IKDataEntity.ModelFactory(entity, config)
