import GlobalSettings from '@/GlobalSettings'

import { IKDataEntity } from 'metaflow-js'
import hillo from 'hillo'

const entity = {
  id: {
    type: IKDataEntity.Types.Integer,
    displayName: 'id',
    tableConfig: {
      align: 'start',
    },
    form: false,
  },
  name: {},
  printTitle: {},
  required: {
    type: IKDataEntity.Types.Boolean,
  },
  multiSelect: {
    type: IKDataEntity.Types.Boolean,
  },
  attrubuteCount: {
    type: IKDataEntity.Types.Integer,
    form: false,
  },
}
const config = {
  load: async function (filter) {
    return (await hillo.get('Category.php', {
      op: 'showAttributeGroup',
      lang: GlobalSettings.getLang(),
      ...filter,
    }))
      .content
  },
  add: function (item) {
    return hillo.postWithUploadFile('Category.php?op=addAttributeGroup', item, {
      showLoading: true,
    })
  },
  edit: function (item) {
    return hillo.postWithUploadFile('Category.php?op=updateAttributeGroup', item, {
      showLoading: true,
    })
  },
  remove: function (id) {
    return hillo.post('Category.php?op=deleteAttributeGroup', { id: id })
  },
}

export default IKDataEntity.ModelFactory(entity, config)
